export const getCurrentDate = () => {
  const date = new Date();
  const day = date.getDate();
  const month = date.toLocaleString('en-Us', {month: 'long'});
  const year = date.getFullYear();

  return `${day} ${month} ${year}`;
};

export const getCelsiusFromKelvin = (temp) => {
  const tempByCelsius = Math.round(temp - 273.15);

  if (tempByCelsius > 0) {
    return `+${tempByCelsius}°`
  }
  
  return `${tempByCelsius}°`
};

export const getNewItemId = (data) => {
  return (data[data.length - 1]?.id + 1) || 1;
};

export const getTodayWeather = (data) => {
  console.log(data);
  return {
    temp: getCelsiusFromKelvin(data.main.temp),
    pressure: `${data.main.pressure} hPa`,
    humidity: `${data.main.humidity}%`,
    windSpeed: `${data.wind.speed} m/s`,
    description: data.weather[0].description,
    icon: data.weather[0].icon,
  }
}

export const getCityNamesData = (data) => {
  const cityNamesData = [];

  data.forEach((item, index) => {
    const formatted = item.formatted;
    const place = item.components.city || item.components.village || item.components.town;

    if (place) {
      cityNamesData.push({
        formatted,
        place,
        id: index
      })
    }
  });

  return cityNamesData;
};

export function debounce(func, wait) {
  let timeout;
  return function (...args) {
    clearTimeout(timeout);
    timeout = setTimeout(() => {
      func.apply(this, args);
    }, wait);
  };
}
